import React from 'react';


const Header = () => (
    <React.Fragment>
        <header>
            <h4 className="white-text marginZero" style={{ position: " relative", top: "60%", textIndent: "15px" }}>My Day</h4>
        </header>
    </React.Fragment>
)

export default Header;


